// https://www.html5rocks.com/en/tutorials/webaudio/intro/

// https://developer.mozilla.org/en-US/docs/Web/API/Web_Audio_API/Visualizations_with_Web_Audio_API

'use strict';
import axios from 'axios';
import Vue from 'vue';

const CHAT_STATE = {
  IDLE: 0,
  THINK: 1,
  REPLY: 2
}

const EMOTION = {
  ANGER: 'anger',
  JOY: 'joy',
  CURIOUS: 'curious',
  LOVE: 'love',
  CAUTIOUS: 'cautious',
  NONE: 'none'
}

declare global {
  interface Window {
    SpeechRecognition: any;
    webkitSpeechRecognition: any;
    AudioContext: any;
    webkitAudioContext: any;
  }
}

const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
const recognition = new SpeechRecognition();
recognition.lang = 'en-US';
recognition.interimResults = true;
recognition.continuous = true;

let vueApp: any = null;

let lastEmotion = "";
let lastOutput = "";

let poemEngineEndPoint = "https://poem.elliottwen.info/poem?v=2&input=";
let audioStreamEndpoint = "https://poem.elliottwen.info/voice?v=2&vid=";

let final_transcript = "";
let interim_transcript = "";

recognition.onstart = () => {
  console.log("Speech Recognition Started");
  vueApp.errorMessage = "";
  //resetTranscripts();
};
recognition.onerror = (e:any) => {
  console.log("Speech Recognition Error");
  vueApp.errorMessage = e.error;
};
recognition.onend = () => {
  console.log("Speech Recognition Ended");
  console.log(final_transcript);
  if(!final_transcript){
    return;
  }
  if(vueApp.listening){
    recognition.start();
    return;
  }
  vueApp.chatState = CHAT_STATE.THINK;
  axios.get(poemEngineEndPoint + final_transcript).then(response => {
    console.log(response.data.output);
    vueApp.chatState = CHAT_STATE.REPLY;
    lastEmotion = response.data.emotion;
    lastOutput = response.data.output;
    let audioURL = audioStreamEndpoint + response.data.vid;
    playWebAudio(audioURL);
    vueApp.setBackgroundBasedOnEmotion(lastEmotion);
  }).catch((e) => {
    console.error(e.message);
  });

  recognition.abort();
};

recognition.onresult = (event:any) => {
  interim_transcript = "";
  for (let i = event.resultIndex; i < event.results.length; ++i) {
    if (event.results[i].isFinal) {
      final_transcript += event.results[i][0].transcript;
    } else {
      interim_transcript += event.results[i][0].transcript;
    }
  }
  if(interim_transcript){
    vueApp.transcript = final_transcript + interim_transcript;
  }
};

//--------------- web audio -------------------

var poemAudioBuffer = null;
const AudioContext = window.AudioContext || window.webkitAudioContext;
const context = new AudioContext();

function playWebAudio(url:string) {
  var request = new XMLHttpRequest();
  request.open('GET', url, true);
  request.responseType = 'arraybuffer';

  // Decode asynchronously
  request.onload = function () {
    context.decodeAudioData(request.response, function (buffer:any) {
      poemAudioBuffer = buffer;
      playSound(poemAudioBuffer);
    }, ()=>{console.error("decode fails")});
  }
  request.send();
}

let isPlayingAudio = false;
function playSound(buffer:any) {
  if(isPlayingAudio){
    return;
  }
  isPlayingAudio = true;
  var source = context.createBufferSource(); // creates a sound source
  source.buffer = buffer;                    // tell the source which sound to play
  source.connect(context.destination);       // connect the source to the context's destination (the speakers)
  source.start(0);                           // play the source now
  source.onended = function() {
    vueApp.chatState = CHAT_STATE.IDLE;
    vueApp.chatbackground = EMOTION.NONE;
    console.log("audio playback ended");
    isPlayingAudio = false
  }
  // note: on older systems, may have to use deprecated noteOn(time);

  // automatically switch to idle mode
}

// end -------------- web audio -------------------


function resetTranscripts(){
  console.warn("resetting");
  final_transcript = "";
  interim_transcript = "";
  vueApp.transcript = "listening...";
}

const formA_url = "https://docs.google.com/forms/d/e/1FAIpQLSezYp4QHYtZA4syn91lMoo-ebs9trsULG707bi3q8di2w-9nA/viewform";
const formB_url = "https://docs.google.com/forms/d/e/1FAIpQLSeBgO_tQwtkLPIeq47sy9jHdIxsvLVuLOfotd-XicQSO0-R5g/viewform";


// vue app
export default Vue.extend(
  {
    name: 'Home',
    created(){
      vueApp = this;
    },
    data() {
      return {
        isChatting: false,
        CHAT_STATE: CHAT_STATE,
        chatState: CHAT_STATE.IDLE,
        chatbackground: EMOTION.NONE,
        listening: false,
        transcript: "listening...",
        errorMessage: "",
      }
    },
    methods: {
      startRecognition(){
        this.listening = true;
        resetTranscripts();
        console.log(recognition);
        recognition.start();
      },
      stopRecognition(){
        this.listening = false;
        recognition.stop();
      },
      setBackgroundBasedOnEmotion(emotion: string){
        console.log(emotion);
        this.chatbackground = emotion;
      },
      openGoogleForm(){
        window.open(formA_url, '_blank');
      }
    }
  }
);


